@import "../../css/variables.css";

.transactionForm {
  margin: 15px auto 0;
  // margin: 100px auto 0; //для тестирования оставляю
  display: flex;
  flex-direction: column;
  align-items: center;

  //background-color: var(--bg-color); //взамен фонового изображения

  @media screen and (min-width: 768px) {
    // margin-top: 28px;
    //background-color: lavender; ////для тестирования оставляю
    //border: 1px solid orange;//для тестирования оставляю
    width: 604px;
    justify-content: space-between;
    padding: 10px 0;
    @media screen and (min-width: 1280px) {
      margin-top: 33px;
      width: 1020px;
      flex-direction: row;
    }
  }
}

.transactionInputsContainer {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
  }
}

.transactionInput {
  display: block;
  background-color: transparent;
  border: 2px solid #ffffff;
  width: 282px;
  padding: 13px 0 13px 20px;

  font-family: var(--font-family); //вероятно перенести в общак?
  font-style: normal; //вероятно перенести в общак?
  font-weight: 400; //вероятно перенести в общак?
  font-size: 12px; //вероятно перенести в общак?
  line-height: 1.167; //вероятно перенести в общак?
  color: var(--additional-text-color);
  letter-spacing: 0.02em; //вероятно перенести в общак?

  &:active,
  &:focus {
    outline: 0;
    outline-offset: 0;
    border-bottom-right-radius: 0px;
    border-bottom-color: var(--accent-color);
  }

  @media screen and (min-width: 768px) {
    border: 2px solid var(--bg-color);
  }
}

.productDescriptionInput {
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  @media screen and (min-width: 768px) {
    width: 192px;
    border-top-right-radius: 0;
  }
  @media screen and (min-width: 1280px) {
    width: 289px;
  }
}

.productCategoryInput {
  border-top: none;
  border-bottom-right-radius: 16px;
  @media screen and (min-width: 768px) {
    border-top: 2px solid var(--bg-color);
    border-bottom-right-radius: 0;
    border-left: none;
    width: 171px;
  }
  @media screen and (min-width: 1280px) {
    width: 188px;
  }
}

.productCategoryContainer {
  position: relative;
  @media screen and (min-width: 768px) {
  }
}

