.productSumContainer {
  position: relative;
  margin-top: 30px;

  display: inline-flex;
  flex-direction: row;
  justify-content: center;

  width: 183px;

  @media screen and (min-width: 768px) {
    width: 104px;
    margin-top: 0;
  }
  @media screen and (min-width: 1280px) {
    width: 124px;
  }
}

