.productSumInput {
  position: relative;

  width: 125px;
  background-color: transparent;
  padding: 13px 45px 13px 0;
  border: 2px solid #ffffff;
  border-radius: 22px 0px 0px 22px;

  font-family: var(--font-family); //вероятно перенести в общак?
  font-style: normal; //вероятно перенести в общак?
  font-weight: 700;
  font-size: 12px; //вероятно перенести в общак?
  line-height: 1.167; //вероятно перенести в общак?
  text-align: right;
  letter-spacing: 0.02em; //вероятно перенести в общак?

  &:active,
  &:focus {
    outline: 0;
    outline-offset: 0;
  }

  @media screen and (min-width: 768px) {
    border-color: var(--bg-color);
    font-weight: 400;
    color: var(--additional-text-color);
    border-radius: 0;
    width: 70px;
    padding-right: 10px;
    border-left: none;
    border-right: none;
  }
  @media screen and (min-width: 1280px) {
    padding-right: 6px;
    width: 80px;
  }
}

.productSumLabel {
  position: absolute;
  top: 15px;
  right: 75px;

  font-family: var(--font-family); //вероятно перенести в общак?
  font-style: normal; //вероятно перенести в общак?
  font-weight: bold;
  font-size: 12px; //вероятно перенести в общак?
  line-height: 1.167; //вероятно перенести в общак?
  letter-spacing: 0.02em; //вероятно перенести в общак?
  text-transform: uppercase;

  color: var(--title-color);
}

